a {
  color: inherit;
  font-size: 1.8vw;
  font-weight: bold;
  text-decoration: none;
}

a:hover, a:active {
  color: #3354f8;
}

.back-link {
  color: inherit;
  align-items: center;
  padding-left: 4vw;
  font-weight: bold;
  text-decoration: none;
  display: inline-flex;
  position: relative;
}

.back-link:hover {
  color: #3354f8;
}

.back-link:before {
  content: "←";
  font-size: 3.8vw;
  transition: color .3s;
  animation: 3s infinite moveArrow;
  position: absolute;
  top: -3.2vh;
  left: 0;
}

@keyframes moveArrow {
  0% {
    left: -7vw;
  }

  50% {
    left: 0;
  }

  51% {
    left: 0;
  }

  100% {
    left: -7vw;
  }
}

li {
  list-style-type: none;
}
